.slider {
  width: 80vw;
  height: auto;
  margin: 2rem auto;

  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;

  overflow: hidden;
  background-color: transparent;
  border-radius: 5px;
  box-shadow: 1px 2px 5px 3px rgba(255, 99, 71, 0.4);
}
